  position: absolute;
  bottom: 9px;
  right: -35px;
}

@keyframes backgroundMove {
  0% {
    right: -35px;
  }

  25% {
    right: 225px;
  }

  50% {
    right: 500px;
  }

  80% {
    right: 880px;
  }

